import React,{Component} from 'react';
import './index.less'
export default class UserInfo extends Component{
    render(){
        return (
            <div className="user-info">
                <span><i className="iconfont icon-yonghufill"></i>{this.props.userInfo.username}</span>
                <span><i className="iconfont icon-yonghufill"></i>{this.props.userInfo.cityName}</span>
            </div>
        )
    }
    //  


}



